"use client"

import { useRef, useEffect, useState, useCallback } from "react"
import ControlPanel from "../ui/control-panel"
import EnergyBars from "../ui/energy-bars"
import { Anchor, RotateCcw, SkipBack, Pause, Play, SkipForward, Zap } from "lucide-react"

export default function SimplePendulum() {
    const canvasRef = useRef<HTMLCanvasElement>(null)
    const [isPlaying, setIsPlaying] = useState(false)
    const [time, setTime] = useState(0)

    const [showGraph, setShowGraph] = useState({ x: true, v: false, a: false })

    const [params, setParams] = useState({
        length: 150,
        gravity: 9.8,
        initialAngle: 10,
    })

    const animationRef = useRef<number>()
    const timeRef = useRef(0)

    const lengthM = params.length / 100
    const omega = Math.sqrt(params.gravity / lengthM)
    const period = (2 * Math.PI) / omega
    const frequency = 1 / period
    const thetaMax = (params.initialAngle * Math.PI) / 180
    const dt = 0.016

    const calculateState = useCallback(
        (t: number) => {
            const theta = thetaMax * Math.cos(omega * t)
            const x = params.length * Math.sin(theta)
            const v = -params.length * omega * thetaMax * Math.sin(omega * t)
            const a = -params.length * omega * omega * thetaMax * Math.cos(omega * t)

            return { theta, x, v, a }
        },
        [thetaMax, omega, params.length],
    )

    const draw = useCallback(
        (ctx: CanvasRenderingContext2D, t: number) => {
            const width = ctx.canvas.width
            const height = ctx.canvas.height

            ctx.fillStyle = "#1a1a2e"
            ctx.fillRect(0, 0, width, height)

            ctx.strokeStyle = "#2a3f5f"
            ctx.lineWidth = 0.5
            for (let i = 0; i < width; i += 40) {
                ctx.beginPath(); ctx.moveTo(i, 0); ctx.lineTo(i, height); ctx.stroke()
            }
            for (let i = 0; i < height; i += 40) {
                ctx.beginPath(); ctx.moveTo(0, i); ctx.lineTo(width, i); ctx.stroke()
            }

            const state = calculateState(t)

            const pivotX = width / 4
            const pivotY = 80

            const bobX = pivotX + params.length * Math.sin(state.theta)
            const bobY = pivotY + params.length * Math.cos(state.theta)

            ctx.fillStyle = "#64748b"
            ctx.fillRect(pivotX - 40, pivotY - 10, 80, 10)

            ctx.strokeStyle = "#4ade80"; ctx.setLineDash([5, 5])
            ctx.beginPath(); ctx.moveTo(pivotX, pivotY); ctx.lineTo(pivotX, pivotY + params.length + 40); ctx.stroke(); ctx.setLineDash([])

            ctx.strokeStyle = "#cbd5e1"; ctx.lineWidth = 2
            ctx.beginPath(); ctx.moveTo(pivotX, pivotY); ctx.lineTo(bobX, bobY); ctx.stroke()

            ctx.fillStyle = "#3b82f6"
            ctx.beginPath(); ctx.arc(bobX, bobY, 20, 0, 2 * Math.PI); ctx.fill()
            ctx.strokeStyle = "#60a5fa"; ctx.lineWidth = 2; ctx.stroke()

            ctx.fillStyle = "#f97316"; ctx.font = "14px monospace"
            ctx.fillText(`θ = ${((state.theta * 180) / Math.PI).toFixed(1)}°`, pivotX + 20, pivotY + 40)

            const graphStartX = width / 2
            const graphWidth = width / 2 - 20
            const graphCenterY = height / 2

            ctx.strokeStyle = "#475569"; ctx.lineWidth = 1
            ctx.beginPath(); ctx.moveTo(graphStartX, graphCenterY); ctx.lineTo(width, graphCenterY); ctx.stroke()

            ctx.beginPath(); ctx.moveTo(graphStartX, 20); ctx.lineTo(graphStartX, height - 20); ctx.stroke()

            const drawCurve = (color: string, getValue: (time: number) => number, scaleFactor: number = 1) => {
                ctx.strokeStyle = color
                ctx.lineWidth = 2
                ctx.beginPath()
                for (let i = 0; i <= graphWidth; i++) {
                    const plotTime = t - ((i / graphWidth) * 2 * period)
                    const val = getValue(plotTime)
                    const amplitudePixel = (thetaMax * params.length)
                    const y = graphCenterY - (val / amplitudePixel) * 80 * scaleFactor

                    if (i === 0) ctx.moveTo(graphStartX + i, y)
                    else ctx.lineTo(graphStartX + i, y)
                }
                ctx.stroke()
            }

            if (showGraph.x) {
                drawCurve("#22d3ee", (tm) => params.length * Math.sin(thetaMax * Math.cos(omega * tm)))
            }

            if (showGraph.v) {
                drawCurve("#4ade80", (tm) => -params.length * omega * thetaMax * Math.sin(omega * tm), 1 / omega)
            }

            if (showGraph.a) {
                drawCurve("#f87171", (tm) => -params.length * omega * omega * thetaMax * Math.cos(omega * tm), 1 / (omega * omega))
            }

            if (showGraph.x) {
                const currentY = graphCenterY - (state.x / (thetaMax * params.length)) * 80
                ctx.fillStyle = "#22d3ee"
                ctx.beginPath(); ctx.arc(graphStartX, currentY, 5, 0, 2 * Math.PI); ctx.fill()

                ctx.strokeStyle = "#22d3ee"; ctx.setLineDash([2, 2]); ctx.lineWidth = 1
                ctx.beginPath(); ctx.moveTo(bobX, bobY); ctx.lineTo(graphStartX, currentY); ctx.stroke(); ctx.setLineDash([])
            }

            ctx.font = "13px monospace"
            const infoX = 20
            const infoY = height - 80

            ctx.fillStyle = "#e2e8f0"; ctx.fillText(`t = ${t.toFixed(2)} s`, infoX, infoY)
            ctx.fillStyle = "#22d3ee"; ctx.fillText(`x = ${state.x.toFixed(1)} cm`, infoX, infoY + 20)
            if (showGraph.v) { ctx.fillStyle = "#4ade80"; ctx.fillText(`v = ${state.v.toFixed(1)} cm/s`, infoX + 150, infoY) }
            if (showGraph.a) { ctx.fillStyle = "#f87171"; ctx.fillText(`a = ${state.a.toFixed(1)} cm/s²`, infoX + 150, infoY + 20) }

            ctx.fillStyle = "#94a3b8"
            ctx.fillText("Mô phỏng thực", pivotX - 40, height - 20)
            ctx.fillText("Đồ thị theo thời gian", graphStartX + 20, height - 20)

        },
        [params.length, params.gravity, thetaMax, omega, period, calculateState, showGraph],
    )

    const animate = useCallback(() => {
        const canvas = canvasRef.current
        if (!canvas) return
        const ctx = canvas.getContext("2d")
        if (!ctx) return

        timeRef.current += dt
        setTime(timeRef.current)

        draw(ctx, timeRef.current)

        if (isPlaying) {
            animationRef.current = requestAnimationFrame(animate)
        }
    }, [isPlaying, draw, calculateState])

    useEffect(() => {
        if (isPlaying) {
            animationRef.current = requestAnimationFrame(animate)
        }
        return () => {
            if (animationRef.current) cancelAnimationFrame(animationRef.current)
        }
    }, [isPlaying, animate])

    useEffect(() => {
        const canvas = canvasRef.current
        if (!canvas) return
        const ctx = canvas.getContext("2d")
        if (!ctx) return
        draw(ctx, timeRef.current)
    }, [params, draw, showGraph])

    const handleStep = (dir: number) => {
        setIsPlaying(false)
        timeRef.current = Math.max(0, timeRef.current + dir * dt)
        setTime(timeRef.current)

        const canvas = canvasRef.current
        if (canvas) draw(canvas.getContext("2d")!, timeRef.current)
    }

    const handleReset = () => {
        setIsPlaying(false)
        timeRef.current = 0
        setTime(0)
        const canvas = canvasRef.current
        if (canvas) draw(canvas.getContext("2d")!, 0)
    }

    const state = calculateState(time)
    const kineticEnergy = 0.5 * 0.5 * (state.v / 100) ** 2
    const potentialEnergy = 0.5 * 9.8 * (lengthM * (1 - Math.cos(state.theta)))

    return (
        <div className="bg-slate-900 text-white font-sans selection:bg-cyan-500 selection:text-white pb-20">
            <div className="max-w-7xl mx-auto space-y-6">

                <header className="flex flex-col md:flex-row justify-between items-center border-b border-slate-700 pb-4">
                    <div className="flex items-center gap-3">
                        <Anchor className="w-8 h-8 text-cyan-400" />
                        <div>
                            <h1 className="text-2xl font-bold bg-gradient-to-r from-cyan-400 to-blue-500 bg-clip-text text-transparent">
                                LAB CON LẮC ĐƠN
                            </h1>
                            <p className="text-slate-400 text-xs">Mô Phỏng Dao Động Điều Hòa</p>
                        </div>
                    </div>

                    <div className="flex gap-2 mt-4 md:mt-0">
                        {/* UNIFIED BUTTON STYLE */}
                        <button
                            onClick={handleReset}
                            className="w-12 h-12 rounded-xl bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
                            title="Làm mới"
                        >
                            <RotateCcw size={20} />
                        </button>
                        <button
                            onClick={() => handleStep(-1)}
                            className="w-12 h-12 rounded-xl bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
                            title="Lùi"
                        >
                            <SkipBack size={20} />
                        </button>
                        <button
                            onClick={() => setIsPlaying(!isPlaying)}
                            className={`w-14 h-14 rounded-2xl flex items-center justify-center transition-all shadow-lg active:scale-95 border border-transparent ${isPlaying
                                    ? 'bg-amber-600 hover:bg-amber-500 shadow-amber-900/20'
                                    : 'bg-cyan-600 hover:bg-cyan-500 shadow-cyan-500/30'
                                } text-white`}
                            title={isPlaying ? "Tạm dừng" : "Chạy"}
                        >
                            {isPlaying ? <Pause size={28} fill="currentColor" /> : <Play size={28} fill="currentColor" className="ml-1" />}
                        </button>
                        <button
                            onClick={() => handleStep(1)}
                            className="w-12 h-12 rounded-xl bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
                            title="Tiến"
                        >
                            <SkipForward size={20} />
                        </button>
                    </div>
                </header>

                <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
                    <div className="lg:col-span-2 space-y-4">
                        <div className="bg-[#16213e] rounded-lg p-4 border border-cyan-900/50 relative shadow-xl">
                            <div className="absolute top-4 right-4 flex gap-2 z-10 bg-[#16213e]/80 p-1 rounded-lg backdrop-blur-sm border border-slate-700">
                                <button onClick={() => setShowGraph(p => ({ ...p, x: !p.x }))} className={`px-2 py-1 text-[10px] font-bold rounded transition-all ${showGraph.x ? 'bg-cyan-500 text-white' : 'bg-slate-700 text-slate-400'}`}>x(t)</button>
                                <button onClick={() => setShowGraph(p => ({ ...p, v: !p.v }))} className={`px-2 py-1 text-[10px] font-bold rounded transition-all ${showGraph.v ? 'bg-green-500 text-white' : 'bg-slate-700 text-slate-400'}`}>v(t)</button>
                                <button onClick={() => setShowGraph(p => ({ ...p, a: !p.a }))} className={`px-2 py-1 text-[10px] font-bold rounded transition-all ${showGraph.a ? 'bg-red-500 text-white' : 'bg-slate-700 text-slate-400'}`}>a(t)</button>
                            </div>

                            <canvas ref={canvasRef} width={800} height={400} className="w-full rounded-lg" />
                        </div>
                    </div>

                    <div className="space-y-4">
                        <ControlPanel title="Thông số con lắc">
                            <div className="space-y-4">
                                <div>
                                    <div className="flex justify-between text-xs mb-1 text-slate-400">
                                        <span>Chiều dài dây (l)</span>
                                        <span className="text-cyan-400 font-mono">{params.length} cm</span>
                                    </div>
                                    <input
                                        type="range"
                                        min="50"
                                        max="250"
                                        value={params.length}
                                        onChange={(e) => setParams({ ...params, length: Number(e.target.value) })}
                                        className="w-full accent-cyan-500 h-2 bg-slate-700 rounded-lg cursor-pointer"
                                    />
                                </div>
                                <div>
                                    <div className="flex justify-between text-xs mb-1 text-slate-400">
                                        <span>Gia tốc g</span>
                                        <span className="text-cyan-400 font-mono">{params.gravity} m/s²</span>
                                    </div>
                                    <input
                                        type="range"
                                        min="1"
                                        max="20"
                                        step="0.1"
                                        value={params.gravity}
                                        onChange={(e) => setParams({ ...params, gravity: Number(e.target.value) })}
                                        className="w-full accent-cyan-500 h-2 bg-slate-700 rounded-lg cursor-pointer"
                                    />
                                </div>
                                <div>
                                    <div className="flex justify-between text-xs mb-1 text-slate-400">
                                        <span>Góc ban đầu θ₀ ≤ 10°</span>
                                        <span className="text-cyan-400 font-mono">{params.initialAngle}°</span>
                                    </div>
                                    <input
                                        type="range"
                                        min="1"
                                        max="10"
                                        value={params.initialAngle}
                                        onChange={(e) => setParams({ ...params, initialAngle: Number(e.target.value) })}
                                        className="w-full accent-cyan-500 h-2 bg-slate-700 rounded-lg cursor-pointer"
                                    />
                                </div>
                            </div>
                        </ControlPanel>

                        <EnergyBars kinetic={kineticEnergy * 2000} potential={potentialEnergy * 2000} total={(kineticEnergy + potentialEnergy) * 2000} />

                        <div className="bg-[#16213e] rounded-lg p-4 border border-cyan-900/50 shadow-lg">
                            <h3 className="text-cyan-400 font-semibold mb-2 flex items-center gap-2"><Zap size={16} /> Công thức</h3>
                            <div className="text-sm text-gray-300 space-y-1 font-mono">
                                <p>Chiều dài l: Từ điểm treo đến tâm vật</p>
                                <div className="w-full h-[1px] bg-gray-700 my-2"></div>
                                <p className="flex justify-between"><span>x = s = l·α</span> <span className="text-cyan-500">Li độ cong</span></p>
                                <p className="flex justify-between"><span>v = s'</span> <span className="text-green-500">Vận tốc</span></p>
                                <div className="w-full h-[1px] bg-gray-700 my-2"></div>
                                <p>ω = √(g/l) = {omega.toFixed(2)} rad/s</p>
                                <p>T = 2π/ω = {period.toFixed(2)} s</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}