import React from 'react';
import { Activity, Power, Settings, Cpu } from 'lucide-react';

// --- 1. GLOBAL LAYOUT (NỀN PHÒNG THÍ NGHIỆM) ---
export const LabLayout = ({ children }: { children: React.ReactNode }) => {
  return (
    <div className="min-h-screen bg-[#02040a] text-cyan-50 font-sans selection:bg-cyan-500/30 selection:text-cyan-200 overflow-x-hidden relative">
      
      {/* Background Grid - Lưới tọa độ */}
      <div className="fixed inset-0 z-0 pointer-events-none opacity-20" 
           style={{
             backgroundImage: 'linear-gradient(rgba(6, 182, 212, 0.1) 1px, transparent 1px), linear-gradient(90deg, rgba(6, 182, 212, 0.1) 1px, transparent 1px)',
             backgroundSize: '40px 40px',
             maskImage: 'radial-gradient(circle at center, black 40%, transparent 100%)' 
           }}>
      </div>
      
      {/* Ambient Glow - Ánh sáng môi trường */}
      <div className="fixed top-[-20%] left-[-10%] w-[50%] h-[50%] bg-cyan-900/10 rounded-full blur-[120px] pointer-events-none"></div>
      <div className="fixed bottom-[-20%] right-[-10%] w-[50%] h-[50%] bg-blue-900/10 rounded-full blur-[120px] pointer-events-none"></div>

      {/* Main Content */}
      <div className="relative z-10 max-w-[1800px] mx-auto p-4 md:p-6 flex flex-col h-screen">
        {children}
      </div>
    </div>
  );
};

// --- 2. HOLO CARD (KHUNG KÍNH MỜ) ---
export const HoloCard = ({ children, className = "", title, icon: Icon, active = false }: { children: React.ReactNode, className?: string, title?: string, icon?: any, active?: boolean }) => (
  <div className={`
    relative backdrop-blur-md rounded-xl border transition-all duration-300 group
    ${active 
      ? 'bg-cyan-950/30 border-cyan-500/50 shadow-[0_0_15px_rgba(6,182,212,0.15)]' 
      : 'bg-[#0a0f1c]/60 border-slate-800/60 hover:border-cyan-500/30 hover:bg-[#0f1629]/60'}
    ${className}
  `}>
    {/* Decorative Corners */}
    <div className="absolute top-0 left-0 w-2 h-2 border-t border-l border-cyan-500/50 rounded-tl opacity-50"></div>
    <div className="absolute top-0 right-0 w-2 h-2 border-t border-r border-cyan-500/50 rounded-tr opacity-50"></div>
    <div className="absolute bottom-0 left-0 w-2 h-2 border-b border-l border-cyan-500/50 rounded-bl opacity-50"></div>
    <div className="absolute bottom-0 right-0 w-2 h-2 border-b border-r border-cyan-500/50 rounded-br opacity-50"></div>

    {title && (
      <div className={`px-4 py-3 border-b flex items-center gap-2 ${active ? 'border-cyan-500/30 bg-cyan-900/20' : 'border-slate-800/50 bg-slate-900/20'}`}>
        {Icon && <Icon size={16} className={active ? 'text-cyan-400' : 'text-slate-500'} />}
        <span className={`text-xs font-bold uppercase tracking-widest ${active ? 'text-cyan-300' : 'text-slate-400'}`}>
          {title}
        </span>
      </div>
    )}
    <div className="p-4">
      {children}
    </div>
  </div>
);

// --- 3. MONITOR SCREEN (MÀN HÌNH HIỂN THỊ) ---
// Dùng cái này bao quanh <canvas>
export const MonitorScreen = ({ children, label = "SIMULATION VIEW" }: { children: React.ReactNode, label?: string }) => (
  <div className="relative rounded-lg overflow-hidden border border-slate-700 bg-black shadow-inner">
    {/* Scanline Effect */}
    <div className="absolute inset-0 z-10 pointer-events-none opacity-10 bg-[linear-gradient(rgba(18,16,16,0)_50%,rgba(0,0,0,0.25)_50%),linear-gradient(90deg,rgba(255,0,0,0.06),rgba(0,255,0,0.02),rgba(0,0,255,0.06))] bg-[length:100%_2px,3px_100%]"></div>
    
    {/* Screen Glare (bóng kính) */}
    <div className="absolute inset-0 z-20 pointer-events-none bg-gradient-to-br from-white/5 to-transparent opacity-30"></div>

    {/* Header nhỏ bên trong màn hình */}
    <div className="absolute top-3 left-3 z-30 flex items-center gap-2 px-2 py-1 bg-black/60 backdrop-blur rounded border border-white/10">
      <div className="w-1.5 h-1.5 rounded-full bg-green-500 animate-pulse"></div>
      <span className="text-[10px] font-mono text-green-500 tracking-wider">{label}</span>
    </div>

    {children}
  </div>
);

// --- 4. NEON BUTTON ---
export const NeonButton = ({ children, onClick, active = false, variant = 'cyan', className="" }: any) => {
  const colors = {
    cyan: active ? "bg-cyan-500/20 text-cyan-300 border-cyan-500 shadow-[0_0_10px_rgba(6,182,212,0.3)]" : "border-slate-700 text-slate-400 hover:text-cyan-400 hover:border-cyan-500/50",
    amber: active ? "bg-amber-500/20 text-amber-300 border-amber-500 shadow-[0_0_10px_rgba(245,158,11,0.3)]" : "border-slate-700 text-slate-400 hover:text-amber-400 hover:border-amber-500/50",
    red: active ? "bg-red-500/20 text-red-300 border-red-500 shadow-[0_0_10px_rgba(239,68,68,0.3)]" : "border-slate-700 text-slate-400 hover:text-red-400 hover:border-red-500/50",
  };
  
  return (
    <button 
      onClick={onClick}
      className={`
        relative px-4 py-2 rounded border font-mono text-sm font-bold uppercase tracking-wider transition-all duration-200
        active:scale-95 flex items-center justify-center gap-2
        ${colors[variant as keyof typeof colors]}
        ${className}
      `}
    >
      {children}
    </button>
  );
};

// --- 5. DATA READOUT (HIỂN THỊ SỐ LIỆU) ---
export const DataReadout = ({ label, value, unit, color="text-cyan-400" }: any) => (
  <div className="flex flex-col border-l-2 border-slate-800 pl-3">
    <span className="text-[10px] text-slate-500 uppercase font-semibold mb-0.5">{label}</span>
    <div className="flex items-baseline gap-1">
      <span className={`text-xl font-mono font-bold leading-none ${color}`}>{value}</span>
      <span className="text-xs text-slate-600 font-mono">{unit}</span>
    </div>
  </div>
);